import './App.css';
import { About } from './About';
import { Contacts } from './Contacts';
import { Employees } from './Employees';
import { Home } from './Home';
import { BrowserRouter , Link, Switch , Route } from 'react-router-dom';


const pages = [
  {
  id: 1,
  route: "/",
  name: "Главная",
  component: Home
  },
  {
  
  id: 2,
  route: "/contacts",
  name: "Контакты",  
  component: Contacts
  },
  {
   
  id: 3,
  route: "/about",
  name: "О нас",  
  component: About
  },
  {
  
  id: 3,
  route: "/employees",
  name: "Сотрудники",  
  component: Employees
  },
  ]

function App() {
  
  
  return (
   <BrowserRouter>
   <div className="App">
      <ul>
      {pages.map(item => (
        <li key={item.id}>
          <Link to={item.route}>
            {item.name}
          </Link>
        </li>
        ))}
      </ul>
      <hr></hr>
      <Switch>
      <div>
        {pages.map(item=> (
          <Route exact path={item.route}>
          <item.component/>
        </Route>
        ))}      
      </div>
      </Switch>
    </div>
    </BrowserRouter>
  );
}

export default App;
